<template>
  <div class="hello">
    
    <div class="lang">
        <button @click="tabLang(index)" v-for="(item , index) in lang" :class="active === index ? 'active' : ''" :key="index">
          {{item.name}}
       </button>
       <button style="background : green;" @click="save">保存</button>
       <button style="background : green; width : 200px;" @click="into">导入上次保存的文件</button>
       <button class="cun" style="background : #000;color : #fff ;" @click="exportJSON">导出文件</button>
       <button :class="active === 5 ? 'active' : ''" class="help" @click="helpAll">帮助</button>
       <input style="margin-left : 10px" type="file" name="" @change="file" accept=".json" id="">
    </div>
    <!-- 中文 -->
   <div class="wrap" v-if="!active">
        <div class="body">
        <li v-for="(item , index) in data.chinese" :key="index">
          <span>中文 {{index+1}} : </span>
          <textarea placeholder="请输入中文" v-model="data.chinese[index].txt" />
          <button @click="delate('cha' , index)">删除</button>
      </li>
      </div>
      <div class="option"> <textarea placeholder="请输入中文" v-model="data.valueCha" style="width:500px;height:80px;"/><button @click="add('cha')">添加</button></div>
   </div>
    <!-- 英文 -->
   <div class="wrap"  v-if="active === 1">
        <div class="body">
        <li v-for="(item , index) in data.english" :key="index">
          <span>英文 {{index+1}} : </span>
          <textarea placeholder="请输入英文" v-model="data.english[index].txt" />
          <button @click="delate('eng' , index)">删除</button>
      </li>
      </div>
      <div class="option"> <textarea v-model="data.valueCha" placeholder="请输入英文" style="width:500px;height:80px;"/><button @click="add('eng')">添加</button></div>
   </div>
  <!-- 阿拉伯 -->

  <div class="wrap"  v-if="active === 2">
        <div class="body">
        <li v-for="(item , index) in data.alb" :key="index">
          <span>阿语 {{index+1}} : </span>
          <textarea placeholder="请输入阿文" v-model="data.alb[index].txt" />
          <button @click="delate('alb' , index)">删除</button>
      </li>
      </div>
      <div class="option"> <textarea v-model="data.valueCha" placeholder="请输入阿文" style="width:500px;height:80px;"/><button @click="add('alb')">添加</button></div>
   </div>

  <!-- 韩语 -->
  <div class="wrap"  v-if="active === 3">
        <div class="body">
          <li v-for="(item , index) in data.han" :key="index">
        <span>韩语 {{index+1}} : </span>
          <textarea placeholder="请输入韩文" v-model="data.han[index].txt" />
          <button @click="delate('han' , index)">删除</button>
         </li>
      </div>
      <div class="option"> <textarea v-model="data.valueCha" placeholder="请输入韩文" style="width:500px;height:80px;"/><button @click="add('han')">添加</button></div>
   </div>

   <!-- 展示 -->
   <div class="wrap" v-if="active===4">
       <div class="top">
           <li v-for="(item , index) in lang" v-show="index != 4" :key="index" >
               <span>{{item.name}}</span>
           </li>
       </div>
       <div class="main">
           <li>
               <div v-for="(item , index) in data.chinese" :key="index">
                  <textarea placeholder="请输入中文" v-model="data.chinese[index].txt" />
               </div>
               <div class="option"><button @click="add('cha' , 1)">添加</button></div>
           </li>
           <li>
               <div v-for="(item , index) in data.english" :key="index">
                  <textarea placeholder="请输入英文" v-model="data.english[index].txt" />
               </div>
           </li>
           <li>
               <div v-for="(item , index) in data.alb" :key="index">
                  <textarea placeholder="请输入阿文" v-model="data.alb[index].txt" />
               </div>
           </li>
           <li>
               <div v-for="(item , index) in data.han" :key="index">
                  <textarea placeholder="请输入韩文" v-model="data.han[index].txt" />
               </div>
           </li>
       </div>
   </div>

   <!-- 帮助 -->
  <div class="wrap" v-if="active===5">
    <li style="margin-top : 100px;">1.登陆工具后点击不同的语言添加不同的文案</li>
    <li>2.每一种语言的序号必须一一对应，例如 ： 中文1 --> 英文1 --> 阿文1 --> 韩语1 ，如果不明白可以看点击显示看是否位于统一行列</li>
    <li>3.显示界面可以对你添加完的语言进行修改</li>
    <li>4.每次觉得添加的差不多了切起点击保存(保存是保存在你浏览器的存储里的)，然后导出文件</li>
    <li>5.保存之后下次进入点击导入的话会导入你上次保存时的状态，但是前提是还是上一次保存时所使用的浏览器和电脑并没有删除过缓存或者做系统重装浏览器</li>
    <li>6.删除按钮尽量不要使用，如果删除一种语言的条目，那么对应的其他语言的条目也必须删除，建议修改操作不要删除</li>
    <li>7.如果还有不懂的地方，请联系我</li>
  </div>
  </div>
</template>

<script>
import FileSaver from 'file-saver'
export default {
  name: 'HelloWorld',
  data () {
    return {
      data : {
          chinese : [

          ],
          english : [
            
          ],
          alb : [

          ],
          han : [

          ],
          valueCha :''
      },
      lang : [
        {
          name : '中文',
        },
        {
          name : '英文',
        },
        {
          name : '阿文',
        },
        {
          name : '韩文',
        },
        {
          name : '显示'
        }
      ],
      active : 0,
      //判断是否继续执行
      show : false
    }
  },
  beforeCreate () {
      let login = sessionStorage.getItem('login')
      if(!login) {
          this.$router.push('/')
          return;
      }
  },
  methods : {
    file (e) {
      let file = e.target.files[0];
      const reader = new FileReader()
      reader.readAsText(file)
      const _this = this
      reader.onload = function () {
        // this.result为读取到的json字符串，需转成json对象
        _this.ImportJSON = JSON.parse(this.result)
        console.log(_this.ImportJSON)
        try {
          _this.ImportJSON[0].zw
            // return window.alert('请导入从本工具导出的文件！')
        } catch (error) {
          _this.show = false
          window.alert('请导入从本工具导出的文件！')
        }
        
        if(!_this.show) {
          _this.show = true
          return;
        }

        //中文
        var Cha = []
        for (let i in  _this.ImportJSON[0].zw) {
            let o = {};
            o['txt'] = _this.ImportJSON[0].zw[i];
            Cha.push(o)
        }
        _this.data.chinese = Cha
        //英文
        var Eng = []
        for (let i in  _this.ImportJSON[1].yw) {
            let o = {};
            o['txt'] = _this.ImportJSON[1].yw[i];
            Eng.push(o)
        }
        _this.data.english = Eng
        //阿文
        var Alb = []
        for (let i in  _this.ImportJSON[2].alb) {
            let o = {};
            o['txt'] = _this.ImportJSON[2].alb[i];
            Alb.push(o)
        }
        _this.data.alb = Alb
        //韩文
        var Han = []
        for (let i in  _this.ImportJSON[3].Han) {
            let o = {};
            o['txt'] = _this.ImportJSON[3].Han[i];
            Han.push(o)
        }
        _this.data.han = Han
      }
    },
    //帮助
    helpAll () {
      this.active = 5
    },
    //倒入上次保存的文件
    into () {
      let intoSave = JSON.parse(localStorage.getItem('ListY'))
      if(intoSave) {
        this.data = intoSave
        alert('倒入成功')
        return;
      }else {
        alert('不好意思，没找到～～～')
        return;
      }
    },
    //本地保存
    save () {
      if(confirm('此次保存是本地存储，不要做清除缓存操作，切换浏览器后会失效，换电脑也是哦，要专一一个浏览器～～')) {
        localStorage.setItem('ListY' , JSON.stringify(this.data))
        alert('本地保存成功～～～')
        return;
      }else {
        alert('不存拉倒，记得自己备份～～～')
        return;
      }
    },
    //删除列表某一项
    delate (type , ind) {
      console.log(this.data.chinese[ind].txt)
      // if(!confirm('如果你要删除这一行请把所有语言的这一行都删除，切记！！！！！')) {
      //   return;
      // }
      if(type === 'cha') {
        this.data.chinese[ind].txt = '';
      }else if (type === 'eng') {
        this.data.english[ind].txt = '';
      }else if (type === 'alb') {
        this.data.alb[ind].txt = '';
      }else if (type === 'han') {
        this.data.han[ind].txt = '';
      }
    },
    exportJSON () {
          //中文转格式
          let cha = this.data.chinese
          let eng = this.data.english
          let alb = this.data.alb
          let han = this.data.han
          let newCha = {}
          cha.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newCha = Object.assign({},newCha,{[keyname] : item.txt})
          })

          //英文转格式
          let newEng = {}
          eng.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newEng = Object.assign({},newEng,{[keyname] : item.txt})
          })

          //阿语转格式
          let newAlb = {}
          alb.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newAlb = Object.assign({},newAlb,{[keyname] : item.txt})
          })

          // 韩语转格式
          let out =[]
          let newHan = {}
          han.forEach((item , index) => {
            var keyname = 'txt' + index+ '';
            newHan = Object.assign({},newHan,{[keyname] : item.txt})
          })
          out.push({'zw' : newCha})
          out.push({'yw' : newEng})
          out.push({'alb' : newAlb})
          out.push({'Han' : newHan})
          // console.log(out)
          // 将json转换成字符串
          const data = JSON.stringify(out)
          // console.log(out)
          const blob = new Blob([data], {type: ''})
          FileSaver.saveAs(blob, 'data.json')
      },

      //添加语言
      add (val , n) {
        if(!this.data.valueCha && !n) {
          alert('不能不输入东西～～～')
          return;
        }
        let defa = this.data.chinese
        if(val==='cha') {
            defa = this.data.chinese
            this.data.english.push({txt : ''})
            this.data.alb.push({txt : ''})
            this.data.han.push({txt : ''})
        }else if(val === 'eng'){
            defa = this.data.english
            this.data.chinese.push({txt : ''})
            this.data.alb.push({txt : ''})
            this.data.han.push({txt : ''})
        }else if(val === 'alb'){
            defa = this.data.alb
            this.data.english.push({txt : ''})
            this.data.chinese.push({txt : ''})
            this.data.han.push({txt : ''})
        }else if(val === 'han'){
            defa = this.data.han
            this.data.english.push({txt : ''})
            this.data.chinese.push({txt : ''})
            this.data.alb.push({txt : ''})
        }
        defa.push({
          txt : this.data.valueCha
        })
        this.data.valueCha = ''
        if(val==='cha') {
             this.data.chinese = defa
        }else if(val === 'eng'){
             this.data.english = defa
        }else if(val === 'alb'){
             this.data.alb = defa
        }else if(val === 'han'){
             this.data.han = defa
        }
        console.log(defa)
      },
      tabLang (ind) {
        this.active = ind
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
html , body , #app  {
    height: 100%;
    width: 100%;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.body {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.body li {
  /* margin-top: 10px; */
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc;
}
.body li button {
  width: 60px;
  height: 20px;
  background: red;
  cursor: pointer;
  border: none;
  color: #fff;
}
.body li textarea {
  border: none;
  padding: 2px;
  width: 100%;
  height: 50px;;
}
.body li span {
  margin-right: 20px;
  width: 80px;
  white-space: nowrap;
}
.option {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.option
button {
  height: 30px;
  width: 100px;
  background: lightblue;
  cursor: pointer;
  margin-top: 10px;
}
.lang {
  width: 100%;
  position: relative;
}
.lang .help {
  height: 30px;
  width: 80px;
  background: lightseagreen;
  margin-left: 10px;
  border: none;
  color: #fff;
  cursor: pointer;
  position: absolute;
  right: 200px;
  top: 0;
}
.lang .cun {
  position: absolute;
  right: 100px;
  top: 0;
}
.lang button {
  height: 30px;
  width: 80px;
  background: lightcoral;
  margin-left: 10px;
  border: none;
  color: #fff;
  cursor: pointer;
}
.lang button.active {
  background: lime;
  color: #000;
}
.wrap .top {
    width: 100%;
    display: flex;
    text-align: center;
    height: 30px;
    border: 1px solid #ccc;
    margin-top: 10px;
    line-height: 30px;
}
.wrap .main {
    display: flex;
}
.wrap .main li {
    flex: 1;
    height: 100px;
}
.wrap .main li div {
    height: 100px;
    margin-top: 10px;
}
</style>



