<template>
  <div class="hello">
    <div class="wrapper">
      <input placeholder="请输入账号" v-model="user" type="text">
      <input placeholder="请输入密码" v-model="pwd" type="text">
      <button @click="login">登陆</button>
    </div>
  </div>
</template>

<script>
import FileSaver from 'file-saver'
export default {
  name: 'HelloWorld',
  data () {
    return {
      user : '',
      pwd : ''
    }
  },
  methods : {
    login () {
      if(this.user === 'duoyuyan' && this.pwd === '666') {
        sessionStorage.setItem('login' , 1)
        this.$router.push('/detail')
      }else {
        alert('账号密码错误，请联系客服')
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
html , body , .hello {
  height: 100%;
  width: 100%;
}
.hello {
  padding-top: 100px;;
}
.wrapper {
  height: 200px;
  width: 400px;
  background: #ccc;
  border-radius: 4px;
  margin: 0 auto;
  text-align: center;
}
.wrapper input {
  width: 90%;
  border-radius: 4px;
  border: none;
  margin: 0 auto;
  height: 25px;;
  margin-top: 20px;
  text-indent: 2em;
}
.wrapper button {
  width: 70%;
  height: 30px;
  margin-top: 20px;
  cursor: pointer;
}
</style>



